import React, { useState } from 'react';
import Col from '../../col';
import Grid from '../../grid';
import Row from '../../row';
import Typography from '../../typography';
import ToggleButtonSet from '../../toggle-button-set';
import ToggleButtonSetLegend from '../../toggle-button-set-legend';
import ToggleButtonList from '../../toggle-button-list';
import ToggleButton from '../../toggle-button';
import ToggleButtonListStateless from '../../toggle-button-list-stateless';

const ToggleButtonExample = () => {
  const [state, setState] = useState({
    items: [
      {
        checked: false,
        label: 'One',
        value: 'one'
      },
      {
        checked: true,
        label: 'Two',
        value: 'Two'
      },
      {
        checked: false,
        label: 'Three',
        value: 'three'
      },
      {
        checked: false,
        label: 'Four',
        value: 'four'
      }
    ]
  });

  const handleChange = e => setState({
    items: [...state.items.map(item => ({ ...item, checked: item.value === e.target.value }))]
  });

  return (
    <Grid>
      <Row>
        <Col>
          <Typography tag="h2">Toggle Button</Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <ToggleButtonSet>
            <ToggleButtonSetLegend>Select a button</ToggleButtonSetLegend>
            <ToggleButtonList>
              <ToggleButton
                name="toggle-button-test-1"
                id="toggle-button-test-11"
                label="One"
                value="test"
              />
              <ToggleButton
                name="toggle-button-test-1"
                id="toggle-button-test-22"
                label="Two"
                value="test2"
              />
              <ToggleButton
                name="toggle-button-test-1"
                id="toggle-button-test-33"
                label="Three"
                value="test3"
              />
            </ToggleButtonList>
          </ToggleButtonSet>
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Toggle Button Pre-Selected</Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <ToggleButtonSet>
            <ToggleButtonSetLegend>Select a button</ToggleButtonSetLegend>
            <ToggleButtonList selectedIndex={2}>
              <ToggleButton
                name="toggle-button-test-2"
                id="toggle-button-test-14"
                label="One"
                value="test"
              />
              <ToggleButton
                name="toggle-button-test-2"
                id="toggle-button-test-25"
                label="Two"
                value="test2"
              />
              <ToggleButton
                name="toggle-button-test-2"
                id="toggle-button-test-36"
                label="Three"
                value="test3"
              />
            </ToggleButtonList>
          </ToggleButtonSet>
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Toggle Button Stateless (BYO-State)</Typography>
          <Typography>See &quot;VIEW CODE&quot; below</Typography>
          <Typography>Use this approach to create your own wrapper to match the shape of your data and how and where the data should be manipulated</Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <ToggleButtonSet>
            <ToggleButtonSetLegend>Select a button</ToggleButtonSetLegend>
            <ToggleButtonListStateless>
              {
                state.items.map((item, k) => (
                  <ToggleButton
                    key={k}
                    checked={item.checked}
                    id={`toggle-button-${k}`}
                    label={item.label}
                    name="toggle-stateless"
                    onChange={handleChange}
                    value={item.value}
                  />
                ))
              }
            </ToggleButtonListStateless>
          </ToggleButtonSet>
        </Col>
      </Row>
    </Grid>
  );
};

export default ToggleButtonExample;
