import React from 'react';
import { node, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-toggle-button-list';

const ToggleButtonListStateless = ({ children, className, ...remainingProps }) => (
  <ul
    className={classnames(CSS_PREFIX, className)}
    {...remainingProps}
  >
    {children}
  </ul>
);

ToggleButtonListStateless.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string
};

ToggleButtonListStateless.displayName = 'ToggleButtonListStateless';

export default ToggleButtonListStateless;
