import React from 'react';
import { node, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-toggle-button-set';

const ToggleButtonSet = ({ children, className, ...remainingProps }) => (
  <fieldset
    className={classnames(CSS_PREFIX, className)}
    {...remainingProps}
  >
    {children}
  </fieldset>
);

ToggleButtonSet.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string
};

ToggleButtonSet.displayName = 'ToggleButtonSet';

export default ToggleButtonSet;
