import React, { Children, cloneElement, useEffect, useRef, useState } from 'react';
import { func, node, number, oneOfType, string } from 'prop-types';
import classnames from 'classnames';
import shortid from 'shortid';

const CSS_PREFIX = 'vds-toggle-button-list';

const ToggleButtonList = ({
  children,
  className,
  id = shortid.generate(),
  onChange,
  selectedIndex,
  ...remainingProps
}) => {
  const [selected, setSelected] = useState(selectedIndex);
  const uniqueId = useRef(id);
  const uid = uniqueId.current;

  useEffect(() => {
    setSelected(selectedIndex);
  }, [selectedIndex]);

  const handleChange = (e, itemIndex) => {
    setSelected(itemIndex);

    if (typeof onChange === 'function') {
      onChange(e, itemIndex);
    }
  };

  return (
    <ul
      className={classnames(CSS_PREFIX, className)}
      id={uid}
      {...remainingProps}
    >
      {
        Children.map(children, (child, k) => cloneElement(child, {
          checked: k === selected,
          id: `${uid}-${k}`,
          onChange: e => handleChange(e, k)
        }))
      }
    </ul>
  );
};

ToggleButtonList.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
   * ID for <ul />
   */
  id: oneOfType([number, string]),

  /**
   * Callback fired when a ToggleButton input value changes.
   */
  onChange: func,

  /**
   * Index of list item to select, where 0 represents the first list item.
   */
  selectedIndex: number
};

ToggleButtonList.displayName = 'ToggleButtonList';

export default ToggleButtonList;
