/* eslint-disable jsx-a11y/no-noninteractive-element-interactions */

import React, { useEffect, useRef, useState } from 'react';
import { bool, func, number, oneOfType, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-toggle-button';
const CSS_STATE = 'vds-state';

const ToggleButton = ({
  checked,
  className,
  focused,
  id,
  label,
  name,
  onBlur,
  onClick,
  onChange,
  onFocus,
  value,
  ...remainingProps
}) => {
  const [focus, setFocus] = useState(focused);
  const uniqueId = useRef(id);
  const uid = uniqueId.current;

  useEffect(() => {
    setFocus(focused);
  }, [focused]);

  const handleFocus = event => {
    setFocus(true);

    if (typeof onFocus === 'function') {
      onFocus(event);
    }
  };

  const handleBlur = event => {
    setFocus(false);

    if (typeof onBlur === 'function') {
      onBlur(event);
    }
  };
  return (
    <li
      className={classnames(`${CSS_PREFIX}-item`, className)}
    >
      <input
        checked={checked}
        className={CSS_PREFIX}
        id={uid}
        name={name}
        onBlur={handleBlur}
        onChange={onChange}
        onClick={onClick}
        onFocus={handleFocus}
        type="radio"
        value={value}
        {...remainingProps}
      />
      <label
        onClick={e => e.stopPropagation()}
        onKeyPress={e => e.stopPropagation()}
        className={classnames(`${CSS_PREFIX}-label`, {
          [`${CSS_STATE}--selected`]: Boolean(checked),
          [`${CSS_STATE}--focus`]: Boolean(focus)
        })}
        htmlFor={uid}
      >
        {label}
      </label>
    </li>
  );
};

ToggleButton.propTypes = {
  /**
   * A Boolean attribute which, if present, indicates that this
   * radio button is the currently selected one in the group
   */
  checked: bool,

  /**
   * @ignore
   */
  className: string,

  /**
   * If true, will put component in a "focused" state.
   */
  focused: bool,

  /**
   * Id for toggle button.
   */
  id: oneOfType([number, string]),

  /**
   * Label for toggle button.
   */
  label: string,

  /**
   * Name for toggle button.
   */
  name: string.isRequired,

  /**
   * Callback fired when the input element loses focus.
   */
  onBlur: func,

  /**
   * Callback fired when the input value changes.
   */
  onChange: func,

  /**
   * Callback fired when the input is clicked.
   */
  onClick: func,

  /**
   * Callback fired when the input element is focused on.
   */
  onFocus: func,

  /**
   * The value attribute serves a special purpose for inputs of type radio:
   * when a form is submitted, only radio buttons which are currently checked are
   * submitted to the server, and the reported value is the value of
   * the value attribute. If the value is not otherwise specified,
   * it is the string "on" by default.
   */
  value: string.isRequired
};

ToggleButton.displayName = 'ToggleButton';

export default ToggleButton;
