import React from 'react';
import { node, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-toggle-button-set-legend';

const ToggleButtonSetLegend = ({ children, className, ...remainingProps }) => (
  <legend
    className={classnames(CSS_PREFIX, className)}
    {...remainingProps}
  >
    {children}
  </legend>
);

ToggleButtonSetLegend.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string
};

ToggleButtonSetLegend.displayName = 'ToggleButtonSetLegend';

export default ToggleButtonSetLegend;
